@use 'sass:map';
@use '../../tokens';
@use '../../utils';

$_switch: tokens.$flexy-comp-switch;
$_transition: 200ms cubic-bezier(0.22, 0.61, 0.36, 1);

.flexy-switch {
  display: inline-flex;
  position: relative;
  height: map.get($_switch, 'track-height');
  width: map.get($_switch, 'track-width');

  &__input {
    display: block;
    border: none;
    cursor: pointer;
    margin: 0px;
    opacity: 0;
    padding: 0px;
    @include utils.absolute-center();
    @include utils.size(100%);
    @include utils.min-size(tokens.$flexy-sys-ref-touch-size);
    -webkit-tap-highlight-color: transparent;
  }

  &__track {
    display: block;
    background-color: tokens.$flexy-sys-palette-grey-25;
    border-radius: map.get($_switch, 'track-shape');
    overflow: hidden;
    pointer-events: none;
    @include utils.absolute-fill();
    transition: background-color $_transition;
  }

  &__thumb {
    display: block;
    pointer-events: none;
    position: absolute;
    left: 0px;
    top: 0px;
    @include utils.size(map.get($_switch, 'track-height'));
    transition: transform $_transition;

    &::before {
      content: '';
      display: block;
      background-color: tokens.$flexy-sys-color-on-primary;
      border-radius: map.get($_switch, 'thumb-shape');
      box-shadow: 0px 0px 3px -1px black;
      @include utils.absolute-center();
      @include utils.size(
        map.get($_switch, 'thumb-width'),
        map.get($_switch, 'thumb-height')
      );
    }
  }

  &__icon,
  &__unselected-icon,
  &__selected-icon {
    font-size: map.get($_switch, 'icon-size');
    @include utils.absolute-center();
  }

  &__icon,
  &__unselected-icon {
    color: tokens.$flexy-sys-palette-grey-60;
    opacity: 1;
  }

  &__selected-icon {
    color: tokens.$flexy-sys-color-primary;
    opacity: 0;
  }

  &__input:focus-visible ~ &__track {
    outline: 2px solid tokens.$flexy-sys-color-primary;
    outline-offset: 2px;
  }

  &__input:checked ~ &__track {
    background-color: tokens.$flexy-sys-color-primary;
  }

  &__input:checked ~ &__thumb {
    transform: translateX(
      calc(map.get($_switch, 'track-width') - map.get($_switch, 'track-height'))
    );
  }

  &__input:checked ~ &__thumb &__icon {
    color: tokens.$flexy-sys-color-primary;
  }

  &__input:checked ~ &__thumb &__unselected-icon {
    opacity: 0;
  }

  &__input:checked ~ &__thumb &__selected-icon {
    opacity: 1;
  }

  &__input:disabled ~ &__track {
    background-color: tokens.$flexy-sys-palette-grey-15;
  }

  &__input:disabled ~ &__thumb {
    filter: grayscale(1) opacity(0.48);
  }
}
